.btn {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 40px;
  padding: 13px 20px;
  margin: 0 auto;
  text-transform: uppercase;
  font-size: 12px;
  color: #323232;
  letter-spacing: 2px;
  text-transform: uppercase;
}

.btn-border {
  border: 3px solid #000;
  transform: perspective(1px) translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
  position: relative;
  transition-property: color;
  transition-duration: 0.3s;
}

.btn-border:before {
  content: "";
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: #000;
  transform: scaleY(0);
  transform-origin: 50% 0;
  transition-property: transform;
  transition-duration: 0.3s;
  transition-timing-function: ease-out;
}

.btn-border:hover {
  color: #fff;
}

.btn-border:hover:before {
  transform: scaleY(1);
}

.btn-play {
  position: absolute;
  top: 50%;
  right: 36%;
  z-index: 3;
  display: inline-block;
  vertical-align: top;
  height: 160px;
  display: flex;
  flex-direction: column;
  transform: translate(-36%, -50%);
}

@media only screen and (max-width: 1000px) {
  .btn-play {
    right: 50%;
    transform: translate(50%, -50%);
  }
}

.btn-play p {
  color: #fff;
  transition: 0.4s;
}

.btn-play #triangle {
  fill: #E6D3BA;
  transition: 0.4s;
}

.btn-play #oval {
  stroke: #E5D1B7;
  transition: 0.4s;
}

.btn-play:hover p {
  color: #E6D3BA;
}

.title__section {
