  top: 130%;
  left: 50%;
  margin-left: -10px;
  display: inline-block;
  vertical-align: top;
  width: 6px;
  height: 6px;
  background-color: #e1caae;
  transform: rotate(45deg);
}

.title_dots:before, .title_dots:after {
  content: '';
  position: absolute;
  top: 20px;
  left: 0;
  display: inline-block;
  vertical-align: top;
  width: 6px;
  height: 6px;
  background-color: #e1caae;
  transform: translateX(-20px);
}

.title_dots:after {
  top: -20px;
  transform: translateX(20px);
}

