  transform: perspective(1px) translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
  position: relative;
  transition: color 0.3s ease-out;
  box-shadow: 0 0 34px 0 rgba(0, 0, 0, 0.1);
}

